@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;1,900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #264653;
}

.container {
  width: 100%;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin-top: 30px;
}

.container input {
  width: 150px;
  height: 30px;
  border: none;
  outline: none;
  padding: 20px;
  text-align: center;
  background-color: transparent;
  transition: all 0.3s linear;
  color: White;
  font-size: 1.2rem;
}

.input-container {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  border-radius: 10px;
}

input:focus {
  width: 300px;
  text-align: start;
  padding: 20px;
}

input:focus::placeholder {
  color: transparent;
}

input::placeholder {
  color: white;
  transition: all 0.3s linear;
}
.container-city {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  gap: 10px;
  margin-top: 50px;
  font-size: 1.5rem;
  font-weight: bold;
  color: rgba(255, 166, 0, 0.761);
}
